@import "./properties.scss";

.profile {
    display: flex;
    align-items: center;
    font-size: 0.8em;
    margin: 0.85em;

    .textContainer {
        display: flex;
        flex-direction: column;
        margin-left: 1em;
        width: 80%;
        min-width: 180px;

        .accountName {
            font-weight: 500;
            margin-bottom: 5px;

            &:hover {
                cursor: pointer;
                text-decoration: underline;
            }
        }
    }

    .caption {
        color: $font-color-secondary;

        &.small {
            font-size: 0.8em;
        }
    }
}

a {
    text-align: left;
    width: 100%;
}